import { Grid, TextField, Typography, Button } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import makeStyles from '@mui/styles/makeStyles';
import React, { useContext, useEffect, useRef } from 'react';
import BreadcrumbsDiv from '../../../component/BreadcrumbsDiv';
import { UserContext } from '../../../contextAPI/UserContext'
import { AddressContext } from '../../../contextAPI/AddressContext'
import { SnackbarContext } from '../../../contextAPI/SnackbarContext'
import Layout from '../../../component/Layout';
import { getAddress } from '../../../action/addressAction';
import { useForm } from 'react-hook-form';
import Loading from '../../../component/Loading';
import { changePassword } from '../../../action/userAction';
import DeleteAddressForm from '../../../component/DeleteAddressForm';
const useStyles = makeStyles(theme => ({
    header: {
        textTransform: 'uppercase',
        fontWeight: '500',
        letterSpacing: '3px',
        marginBottom: theme.spacing(5),
    },
    headerAcc: {
        textTransform: 'uppercase',

    },
    item: {
        margin: '10px 0',
    },
    underline: {
        textDecoration: 'underline'
    },
    deleteline: {
        textDecoration: 'underline',
        color: theme.palette.error.main
    }
}))

const Info = () => {
    const classes = useStyles();
    const { user, userDispatch } = useContext(UserContext);
    const { address, addressDispatch } = useContext(AddressContext)
    const { openSnackbarDispatch } = useContext(SnackbarContext)
    const { email, fullName, phoneNumber } = user.userDetails

    const {
        register,
        handleSubmit,
        setError,
        clearErrors,
        control,
        setValue,
        watch,
        reset,
        formState: { errors }
    } = useForm();

    const newPassword = useRef({});
    newPassword.current = watch("newPassword", "");

    const onSubmit = async (data) => {
        const res = await changePassword(data);
        console.log(res);
        if (res.error) {
            setError(res.type, {
                type: "manual",
                message: res.error
            })
        } else {
            reset({
                password: "",
                newPassword: "",
                passwordConfirm: ""
            });
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: res.msg,
                    type: "success"
                }
            })
        }
    }

    useEffect(() => {
        getAddress(addressDispatch);
    }, [])

    return (
        <Layout headfoot>
            <BreadcrumbsDiv link={"/info"} content={"Thng tin"} />
            <Grid container>
                <Grid item xs={3} />
                <Grid item xs={6} >
                    <Typography className={classes.header} variant="h3" color="initial">Thng tin</Typography>
                    <Accordion variant="outlined" square>
                        <AccordionSummary

                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel1a-content"
                            id="panel1a-header"
                        >
                            <Typography className={classes.headerAcc}>Thng tin</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <Grid className={classes.item} container>
                                <Grid item xs={3}>H tn</Grid>
                                <Grid item xs={8}>{fullName}</Grid>
                            </Grid>
                            <Grid className={classes.item} container>
                                <Grid item xs={3}>Email</Grid>
                                <Grid item xs={9}>{email}</Grid>
                            </Grid>
                            <Grid className={classes.item} container>
                                <Grid item xs={3}>S in thoi</Grid>
                                <Grid item xs={9}>{phoneNumber}</Grid>
                            </Grid>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion variant="outlined" square>
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel2a-content"
                            id="panel2a-header"
                        >
                            <Typography className={classes.headerAcc}>Thng tin giao hng</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            {address?.address.length > 0 ?
                                address.address.map((el, index) =>
                                    <Grid className={classes.item} key={index} container>
                                        <Grid item xs={3}>{index + 1}</Grid>
                                        <Grid item xs={8}>
                                            <Grid container>
                                                <Grid item xs={3}>H tn:</Grid>
                                                <Grid item xs={9}>{el.name}</Grid>
                                            </Grid>
                                            <Grid container>
                                                <Grid item xs={3}>a ch:</Grid>
                                                <Grid item xs={9}>{el.address}</Grid>
                                            </Grid>
                                            <Grid container>
                                                <Grid item xs={3}>S in thoi:</Grid>
                                                <Grid item xs={9}>{el.phoneNumber}</Grid>
                                            </Grid>
                                        </Grid>
                                        <Grid item xs={1}>
                                            <DeleteAddressForm id={el._id} />
                                        </Grid>
                                    </Grid>

                                )
                                :
                                <Typography alignItems="center">Khng c thng tin giao hng</Typography>
                            }
                        </AccordionDetails>
                    </Accordion>
                    <Accordion variant="outlined" square>
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel3a-content"
                            id="panel3a-header"
                        >
                            <Typography className={classes.headerAcc}>i mt khu</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <Grid alignItems="center" className={classes.item} container>
                                <Grid item xs={4}>Nhp mt khu hin ti</Grid>
                                <Grid item xs={8}>
                                    <TextField
                                        name="password"
                                        type="password"
                                        variant="outlined"
                                        size="small"
                                        margin="normal"
                                        required
                                        fullWidth
                                        label="Mt khu"
                                        id="password"
                                        {...register("password", {
                                            required: "Vui lng nhp mt khu!",
                                        })}
                                        error={Boolean(errors.password)}
                                        helperText={errors.password?.message}
                                    />
                                </Grid>
                            </Grid>
                            <Grid alignItems="center" className={classes.item} container>
                                <Grid item xs={4}>Nhp mt khu mi</Grid>
                                <Grid item xs={8}>
                                    <TextField
                                        name="newPassword"
                                        type="password"
                                        variant="outlined"
                                        size="small"
                                        margin="normal"
                                        required
                                        fullWidth
                                        label="Mt khu"
                                        id="newPassword"
                                        {...register("newPassword", {
                                            required: "Vui lng nhp mt khu!",
                                        })}
                                        error={Boolean(errors.newPassword)}
                                        helperText={errors.newPassword?.message}
                                    />
                                </Grid>
                            </Grid>
                            <Grid alignItems="center" className={classes.item} container>
                                <Grid item xs={4}>Xc nhn mt khu</Grid>
                                <Grid item xs={8}>
                                    <TextField
                                        variant="outlined"
                                        size="small"
                                        margin="normal"
                                        fullWidth
                                        name="passwordConfirm"
                                        label="Nhp li mt khu"
                                        type="password"
                                        id="passwordConfirm"
                                        {...register("passwordConfirm", {
                                            required: "Vui lng nhp mt khu!",
                                            validate: value =>
                                                value === newPassword.current || "Mt khu nhp li khng ng!"
                                        })}
                                        error={Boolean(errors.passwordConfirm)}
                                        helperText={errors.passwordConfirm?.message}
                                    />
                                </Grid>
                            </Grid>
                            <Grid alignItems="center" className={classes.item} container>
                                <Grid item xs={4}></Grid>
                                <Grid item xs={8}>
                                    <Button onClick={handleSubmit(onSubmit)} color="primary" variant="contained">Xc nhn</Button>
                                </Grid>
                            </Grid>
                        </AccordionDetails>
                    </Accordion>
                </Grid>
                <Grid item xs={3} />
            </Grid>
            <Loading open={address.loading} />
        </Layout>
    );
};

export default Info;